<template>
<div>
	<div class="page-header">
		<mt-header fixed title="My Account Profile">
			<router-link :to="{name: 'Dashboard'}" slot="left">
				<mt-button icon="back">Back</mt-button>
			</router-link>
		</mt-header>
	</div>

	<div class="page-body">
		<mt-field label="ContactName" type="text" v-model="editForm.ContactName"></mt-field>
		<mt-field label="CompanyName" type="text" v-model="editForm.CompanyName"></mt-field>
		<mt-field label="Address" type="text" v-model="editForm.Address"></mt-field>
		<mt-field label="Address2" type="text" v-model="editForm.Address2"></mt-field>
		<mt-field label="City" type="text" v-model="editForm.City"></mt-field>

		<c-picker field-label="State" :field-value="editForm.State" 
			:picker-list="stateList" picker-value-key="ShortName" @selectedChanged="onStateChanged">
		</c-picker>

		<mt-field label="Zipcode" type="text" v-model="editForm.Zipcode"></mt-field>
		<mt-field label="OfficePhone" type="text" v-model="editForm.OfficePhone"></mt-field>

		<div>
			<mt-button @click.native="editItemCancel">Cancel</mt-button>
			<mt-button type="primary" @click.native="editItemSubmit">Save Change</mt-button>
			<mt-button type="primary" @click.native="changePasswordSubmit">Change Password</mt-button>
			<mt-button type="primary" :disabled="enablePO" @click.native="enablePOSubmit">Enable PO</mt-button>
		</div>
	</div>
</div>
</template>

<script>
import { Indicator, Toast, MessageBox  } from 'mint-ui';
import axios from 'axios';
import Utils from '../utils/index.js';
import Customer from '../api/Customer.js';
import TaxRate from '../api/TaxRate.js';

import ComboPicker from '../components/ComboPicker.vue';

export default {
	name: 'Profile',
	components:{
		'c-picker': ComboPicker
	},
	data(){
		return {
			editForm: {
				State: ''
			},
			stateList:[]
		}
	},
	computed:{
		enablePO(){
			if(Object.keys(this.editForm).length > 0)
			{
				return this.editForm.CustomerID > 0;
			}

			return false;
		}
	},
	methods:{
		$message(data)
		{
			Toast({message: data.message, duration: 3000});
		},
		showNetworkError()
		{
			this.$message({message: 'The Web Server may be too busy, please try again at a later time.', type: 'error'});
		},
		onStateChanged(obj)
		{
			this.editForm.ShipState = obj.ShortName;
		},
		editItemCancel()
		{
			this.$router.push({ name: 'Dashboard' });
		},
		enablePOSubmit()
		{
			this.$router.push({ name: 'ChangeCustomer' });
		},
		changePasswordSubmit()
		{
			this.$router.push({ name: 'ChangePassword' });
		},
		editItemSubmit() 
		{
			Indicator.open();

			let entity = Object.assign({}, this.editForm);
			entity.IsDirty = true;
			entity.IsNew = false;
			entity.IsDeleted = false;

			let customer = Utils.getCustomer();
			entity.ModifiedBy = customer.Email;

			Customer.Save(entity).then((res) => {
				Indicator.close();
				if(res.data.OK){	
					this.$message({message: 'Your profile has been successfully modified.',type: 'success'});
					this.$router.push({ name: 'Dashboard' });	
				}
				else
				{
					this.$message({message: res.data.Error,type: 'error'});
					console.log(res.data);
				}
			}).catch(err=>{
				Indicator.close();
				this.showNetworkError();
				console.log(err);
			});
		}
	},
	mounted()
	{
		let customer = Utils.getCustomer();
		Customer.GetByPK(customer.CustomerPK).then((res) => {
			this.editForm = res.data;
		}).catch(err=>{
			Indicator.close();
			this.showNetworkError();
			console.log(err);
		});

		this.stateList = this.$store.state.stateList;
		if(this.stateList.length == 0)
		{
			TaxRate.GetAllState().then((res) => {
				this.stateList = res.data;
				this.$store.commit('setStateList', this.stateList);
			}).catch(err=>{
				console.log(err);
			});
		}
	}
}
</script>

<style scoped>
	.page-header{
		margin-bottom: 40px;
	}
</style>
