<template>
<div>
	<div class="page-header">
		<mt-header fixed title="Change Password">
			<router-link :to="{name: 'Dashboard'}" slot="left">
				<mt-button icon="back">Back</mt-button>
			</router-link>
		</mt-header>
	</div>
	
	<div class="page-body">
		<mt-field label="Password" type="password" v-model="Password"></mt-field>
		<mt-field label="NewPassword" type="password" v-model="NewPassword"></mt-field>
		<mt-field label="ConfirmNewPassword" type="password" v-model="ConfirmNewPassword"></mt-field>

		<div>
			<mt-button @click.native="editItemCancel">Cancel</mt-button>
			<mt-button type="primary" @click.native="editItemSubmit">Change Password</mt-button>
		</div>
	</div>
</div>
</template>

<script>
import { Indicator, Toast, MessageBox  } from 'mint-ui';
import Utils from '../utils/index.js';
import BizAspnetMemberShip from '../api/BizAspnetMemberShip.js';

export default {
	name: 'ChangePassword',
	data(){
		return {
			Password: '',
			NewPassword: '',
			ConfirmNewPassword: ''
		}
	},
	methods:{
		$message(data)
		{
			Toast({message: data.message, duration: 3000});
		},	
		$alert(message, title)
		{
			MessageBox.alert(message, title, {confirmButtonText: 'OK'});
		},			
		showNetworkError()
		{
			this.$message({message: 'The Web Server may be too busy, please try again at a later time.', type: 'error'});
		},
		editItemCancel()
		{
			this.$router.push({ name: 'Profile' });
		},
		editItemSubmit() 
		{
			if(this.Password == '')
			{
				this.$alert('The password can not be empty.','Warning');
				return;
			}

			if(this.NewPassword == '')
			{
				this.$alert('The new password can not be empty.','Warning');
				return;
			}

			if(this.ConfirmNewPassword == '')
			{
				this.$alert('The confirm password can not be empty.','Warning');
				return;
			}

			if(this.ConfirmNewPassword == this.NewPassword)
			{
				this.$alert('The new password does not match the confirmed password you typed.  Please try it again','Warning');
				return;
			}

			Indicator.open();	
			let customer = Utils.getCustomer();
			BizAspnetMemberShip.ChangePassword(customer.Email, entity.Password, entity.NewPassword).then((res) => {
				Indicator.close();
				if(res.data.OK){	
					this.$message({message: 'Congratulation! Your password has been changed successfully.',type: 'success'});
					this.$router.push({ name: 'Profile' });	
				}
				else
				{
					this.$message({message: 'The old password you typed does not match the record in the database.  Please try it again.',type: 'error'});
					console.log(res.data);
				}
			}).catch(err=>{
				Indicator.close();
				this.showNetworkError();
				console.log(err);
			});
			
		}
	}
}
</script>

<style scoped>
	.page-header{
		margin-bottom: 40px;
	}
</style>
