<template>
<div>
	<div class="page-header">
		<mt-header fixed title="Change Customer">
			<router-link :to="{name: 'Dashboard'}" slot="left">
				<mt-button icon="back">Back</mt-button>
			</router-link>
		</mt-header>
	</div>
	
	<div class="page-body">
		<mt-field label="CustomerID" type="number" v-model="CustomerID"></mt-field>
		<mt-field label="City" type="text" v-model="City"></mt-field>
		<mt-field label="Zipcode" type="number" v-model="Zipcode"></mt-field>

		<c-picker field-label="State" :field-value="State" 
			:picker-list="stateList" picker-value-key="ShortName" @selectedChanged="onStateChanged">
		</c-picker>

		<div>
			<mt-button @click.native="editItemCancel">Cancel</mt-button>
			<mt-button type="primary" @click.native="editItemSubmit" >Save Change</mt-button>
		</div>
	</div>
</div>
</template>

<script>
import { Indicator, Toast, MessageBox  } from 'mint-ui';
import Utils from '../utils/index.js';
import BizAspnetMemberShip from '../api/BizAspnetMemberShip.js';
import TaxRate from '../api/TaxRate.js';

export default {
	name: 'ChangeCustomer',
	data(){
		return {
			CustomerID: 0,
			City: '',
			State: '',
			Zipcode: '',
			stateList:[]
		}
	},
	methods:{
		$message(data)
		{
			Toast({message: data.message, duration: 3000});
		},
		showNetworkError()
		{
			this.$message({message: 'The Web Server may be too busy, please try again at a later time.', type: 'error'});
		},
		onStateChanged(obj)
		{
			this.State = obj.ShortName;
		},
		editItemCancel()
		{
			this.$router.push({ name: 'Profile' });
		},
		editItemSubmit() 
		{
			Indicator.open();
			BizAspnetMemberShip.IsValidCustomer(this.CustomerID, this.City, this.State, this.Zipcode).then((res) => {
				if(!res.data.OK){
					Indicator.close();	
					this.$message({message: 'Customer account information you entered does nott match the record in the database. Please try it again.',type: 'error'});
					console.log(res.data);
				}
				else
				{
					let customer = Utils.getCustomer();
					BizAspnetMemberShip.ChangeCustomerAccount(customer.Email).then((res) => {
						if(!res.data.OK){
							Indicator.close();
							this.$message({message: 'Your request can not be finished successfully.  Please retry it again later.',type: 'error'});
							console.log(res.data);
						}
						else
						{
							Indicator.close();
							this.$message({message: 'Your pay by PO option has been enabled successfully.',type: 'success'});
							this.$router.push({ name: 'Profile' });	
						}
					}).catch(err=>{
						Indicator.close();
						this.showNetworkError();
						console.log(err);
					});
				}
			}).catch(err=>{
				Indicator.close();
				this.showNetworkError();
				console.log(err);
			});
		}
	},
	mounted()
	{
		TaxRate.GetAllState().then((res) => {
			for(let i = 0; i < res.data.length; i++)
			{
				this.stateList.push(res.data[i])
			}
		}).catch(err=>{
			this.showNetworkError();
			console.log(err);
		});
	}
}
</script>

<style scoped>
	.page-header{
		margin-bottom: 40px;
	}
</style>
