	{
		let token = Utils.getUserToken();
		let para = {
			ParameterNames: 'pkList',
			Parameters: [pkList]
		};

		Object.assign(token, para);

		let url =  Utils.getBaseUrl() + 'DataService/GetCustomerResult/DeleteByPKList?access_token=8A1F24DB-A959-4D13-8937-6966B0DC1EE7';
		return axios.post(url, token);
	}

